import React, { useState } from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import {FormControl, InputGroup, Button,Container} from 'react-bootstrap';
import SearchIcon from '@material-ui/icons/Search';


const FotoShop=()=>{
  const accessKey ="ItOE2NlfWxuqFl2OxftVpPzL6RlwVlB_w0UBJb1UUTQ";
    const [search, updateSearch] = useState();
    const [results, newImg] = useState([]);

    const fetchImages=()=>{
      fetch(`https://api.unsplash.com/search/photos/?client_id=${accessKey}&query=${search}`)
      .then(res=>res.json())
      .then(data=>{
        console.log(data);
        newImg(data.results)
      })
    }
    
    const query=(e)=>{
        // console.log(e.target.value);
        updateSearch(e.target.value);
    }
    // const searchImg=()=>{
    //     newImg(search);
    //     console.log(search);
    // }
    
   
    return(
        <>
    <Container >
    {/* <h2 className="mt-3 text-center title text-dark">Foto<span class="text-primary">Shop</span></h2> */}
  <InputGroup className="mb-5 mt-4">
    <FormControl
      placeholder="Searh images"
      aria-describedby="basic-addon2"
      className="py-4"
      onChange={query}
    />
    <InputGroup.Append>
    <Tooltip title="search">
      <Button variant="primary" onClick={fetchImages}>
        <SearchIcon />
      </Button>
    </Tooltip>
    </InputGroup.Append>
  </InputGroup>

<div className="gallery">


{
  results.map((item)=>{
    return (<a href={item.urls.regular} target="blank">
      <div className="imgCon">
    
      <img src={item.urls.regular} key={item.id} alt="unsplash_images" class="gallery_image" />
      </div>
      </a>

    )
      })
}


</div>



</Container>


    </>);

}


export default FotoShop;